table {
    border-collapse: collapse;
    width: 100%;
    font-family: 'Open Sans', sans-serif;
}

thead {
    background-color: #FFFFFF;
    box-shadow: 0px 3px 10px rgba(0, 0, 0, 0.1);
}

th, td {
    padding: 12px;
    text-align: left;
    user-select: none;
}

th {
    font-weight: 600;
    font-size: 14px;
    color: #4A4A4A;
}

td {
    font-size: 14px;
    color: #828282;
}

tr:nth-child(even) {
    background-color: #F5F5F5;
}

tr:hover {
    background-color: #FAFAFA;
}

@media screen and (max-width: 600px) {
    th, td {
        padding: 8px;
    }

    th {
        font-size: 12px;
    }

    td {
        font-size: 13px;
    }
}

tr {
    /* задаем область захвата элемента */
    touch-action: none;
}


.draggable {
    /* устанавливаем стили для перетаскиваемого элемента */
    background-color: #f9c2ff;
}

/* определяем стили для активного состояния элемента */
.draggable:active {
    background-color: #bd7aff;
}

button {
    background-color: #4A4A4A;
    border: none;
    color: white;
    padding: 8px 16px;
    text-align: center;
    text-decoration: none;
    display: inline-block;
    font-size: 16px;
    margin: 4px 2px;
    cursor: pointer;
}

button:hover {
    background-color: #3e8e41;
}

button:disabled {
    opacity: 0.5;
    cursor: not-allowed;
}

.button-container {
    text-align: center;
}